<!--  -->
<template>
  <div>
    <el-tabs v-model="tabPosition" type="border-card">
      <el-tab-pane label="" name="line">
        <charts :chartData="chartData" chartType="line" ref="line"></charts>
        <el-button-group>
          <el-button type="primary" @click="add"></el-button>
          <el-button type="danger" @click="remove"></el-button>
        </el-button-group>
      </el-tab-pane>
      <el-tab-pane label="" name="bar">
        <charts :chartData="chartData" chartType="bar" ref="bar"></charts>
        <el-button-group>
          <el-button type="primary" @click="add"></el-button>
          <el-button type="danger" @click="remove"></el-button>
        </el-button-group>
      </el-tab-pane>
      <el-tab-pane label="" name="pie">
        <charts :chartData="chartData" chartType="pie" ref="pie"></charts>
        <el-button-group>
          <el-button type="primary" @click="add"></el-button>
          <el-button type="danger" @click="remove"></el-button>
        </el-button-group>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import charts from "../../components/charts";
export default {
  name: "charts-demo",
  components: {
    charts
  },
  data() {
    return {
      tabPosition: "line",
      chartData: [["1", 10], ["2", 20]],
      chartRefs: ["line", "bar", "pie"]
    };
  },
  props: {},
  computed: {},
  methods: {
    add() {
      var x = "" + (this.chartData.length + 1);
      var y = Math.ceil(Math.random() * 100);
      this.chartData.push([x, y]);
      for (let index in this.chartRefs) {
        this.$refs[this.chartRefs[index]].drawChart();
      }
    },
    remove() {
      if (this.chartData.length <= 2) {
        this.$message({
          message: "",
          type: "warning"
        });
        return;
      }
      this.chartData.splice(this.chartData.length - 1, 1);
      for (let index in this.chartRefs) {
        this.$refs[this.chartRefs[index]].drawChart();
      }
    }
  },
  watch: {
    // tabPosition(val) {
    //   this.$refs[val].drawChart();
    // }
  },
  created() {}
};
</script>

<style scoped>

</style>


